import { MessageItem } from '../im.type';

export function translateGroupSystemNotice(message: MessageItem) {
  const groupName = message.payload?.groupProfile.name || message.payload?.groupProfile.groupID;
  switch (message.payload.operationType) {
    case 1:
      return `${message.payload.operatorID} ${groupName}`;
    case 2:
      return `${groupName}`;
    case 3:
      return `${groupName}`;
    case 4:
      return `${message.payload.operatorID}${groupName}`;
    case 5:
      return `${groupName} ${message.payload.operatorID}`;
    case 6:
      return `${message.payload.operatorID}${groupName}`;
    case 7:
      return `${message.payload.operatorID}${groupName}`;
    case 8:
      return `${groupName}`;
    case 9:
      return `${message.payload.operatorID}${groupName}`;
    case 10:
      return `${message.payload.operatorID}${groupName}`;
    case 255:
      return ': ' + message.payload.userDefinedField;
  }
}
