import * as LibGenerateTestUserSig from "./generate-usersig.min";

/*
 * Module:   GenerateTestUserSig
 *
 * Function:  UserSigUserSig 
 *            SDKAppIDUserID  EXPIRETIME  HMAC-SHA256
 *
 * Attention:  App 
 *
 *             UserSig SDK 
 *             SECRETKEY  Web 
 *             UserSig 
 *
 *             UserSig  App  UserSig
 *             App
 *
 * Referencehttps://cloud.tencent.com/document/product/647/17275#Server
 */
export function genTestUserSig(userID: string) {
  /**
   *  SDKAppId SDKAppId
   *
   * [](https://console.cloud.tencent.com/rav )  SDKAppId
   * 
   */
  const SDKAPPID = 1400440675;

  /**
   * 
   * <p>
   * 
   * 7 x 24 x 60 x 60 = 604800 = 7 
   */
  const EXPIRETIME = 604800;

  /**
   * 
   *
   * step1. [](https://console.cloud.tencent.com/rav )
   * step2. 
   * step3.  UserSig 
   *
   * Demo UserSig 
   * https://cloud.tencent.com/document/product/647/17275#Server
   */
  const SECRETKEY = '6b4ede60d47a82d913c91658b6fbb4a537a7c83da8d210c1112c48a24e76e5f2';

  const userSig = new LibGenerateTestUserSig(SDKAPPID, SECRETKEY, EXPIRETIME).genTestUserSig(userID);
  return {
    SDKAppID: SDKAPPID,
    userSig: userSig
  };
}
